.StartGame {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.StartGame h1 {
  margin-top: 10vh;
}

.Chooses {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.cross {
  width: 5vh;
  height: 5vh;
}

.circle {
  width: 5vh;
  height: 5vh;
  cursor: pointer;
}

img:hover {
  transform: scale(2);
}
