.Field {
  width: 17vh;
  height: 17vh;
  border: 2px solid black;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Field:hover {
  background: rgba(233, 60, 60, 0.2);
}
.cross {
  width: 12vh;
  height: 12vh;
  cursor: pointer;
}

.circle {
  width: 12vh;
  height: 12vh;
  cursor: pointer;
}
.cross:hover {
  transform: scale(1);
}
.circle:hover {
  transform: scale(1);
}
